ter;\n  width: 100%;\n  padding: 0.5rem;\n  border-bottom: 1px solid var(--highlight);\n  cursor: pointer;\n}\n\n.task-item:hover {\n  background-color: var(--border-muted);\n  transition: 0.3s ease;\n}\n\n.task-checkbox {\n  align-self: flex-start;\n  width: 1rem;\n  height: 1rem;\n  margin-top: 0.25rem;\n  background-color: transparent;\n  border: 2px solid var(--text);\n  border-radius: 50%;\n}\n\n.task-item-right {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 0.5rem;\n  width: 100%;\n}\n\n.task-text > * {\n  display: -webkit-box;\n  -webkit-line-clamp: 2;\n  -webkit-box-orient: vertical;\n  overflow: hidden;\n  padding-left: 1rem;\n}\n\n.task-title {\n  -webkit-line-clamp: 1;\n}\n\n.task-description {\n  color: var(--text-muted);\n  font-size: 0.8rem;\n}\n\n.task-details {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 1rem;\n}\n\n@media (max-width: 425px) {\n  .task-details {\n    display: none;\n  }\n}\n\ndialog {\n  position: fixed;\n  top: 45%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 100%;\n  max-width: 500px;\n  background: var(--gradient);\n  padding: 1rem;\n  margin: 0;\n  border: 1px solid var(--border);\n  border-top: 1px solid var(--highlight);\n  border-radius: 6px;\n  box-shadow: inset 0 1px 0 var(--highlight), var(--shadow);\n  z-index: 1;\n  overflow: visible;\n}\n\ndialog:hover {\n  background: var(--gradient);\n}\n\ndialog::backdrop {\n  background-color: rgba(0, 0, 0, 0.5);\n}\n\n.form-container {\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n  height: auto;\n}\n\n.form-container input {\n  background: transparent;\n  padding: 0.25rem 0.25rem 0.5rem 0.25rem;\n  border: none;\n  border-bottom: 1px solid var(--highlight);\n  outline: none;\n}\n\n.form-title {\n  font-size: 1.25rem;\n  font-weight: 600;\n}\n\n.form-title::placeholder {\n  color: var(--text-muted);\n}\n\n.form-title.invalid {\n  border-bottom: 1px solid var(--secondary)